body {
  margin: 0;
  padding: 0;
  font-family: 'Helvetica', Arial, sans-serif;
  background-color: #f5f5f5;
}

.container {
  max-width: 900px;
  margin: 40px auto;
  background-color: #fff;
  padding: 20px 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.header {
  text-align: center;
  margin-bottom: 30px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ccc;
}

.header h1 {
  margin: 0;
  font-size: 2.5rem;
  color: #333;
}

.header h2 {
  margin: 10px 0 0;
  font-size: 1.5rem;
  color: #666;
}

section {
  margin-bottom: 40px;
}

section h3 {
  border-bottom: 2px solid #007acc;
  padding-bottom: 5px;
  margin-bottom: 20px;
  color: #007acc;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.skills-container > div {
  flex: 1 1 300px;
}

.education-item, .experience-item {
  margin-bottom: 20px;
}

.education-item h4, .experience-item h4 {
  margin: 0;
  font-size: 1.2rem;
  color: #333;
}

footer {
  text-align: center;
  padding: 20px 0;
  border-top: 1px solid #ccc;
  color: #999;
}
